body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-header {
  background-color: #000;
  height: 100px;
  width: 100%;
  color: white;
  display: table;
  overflow: visible
}

.app-title {
  text-align: center;
  font-weight: bold;
  font-size: 2rem;
  display: table-cell;
  vertical-align: middle;
}

.page-content {
  display: grid;
  background-color: #EFF3F6;
  height: 100vh;
}

@media only screen and (min-width: 461px)  and (max-width: 780px) {
  .app-title {
    font-size: 1.5rem;
  }
}

@media only screen and (max-width: 460px) {
  .app-title {
    font-size: 1.25rem;
  }
}

@media only screen and (max-width: 359px) {
  .app-title {
    font-size: 1rem;
  }
}
.notifications {
    display: table-cell;
    vertical-align: middle;
    width: 80px;
}

.notifications-list-popup {
    position: absolute;
    right: 31px;
    width: 350px;
    height: 400px;

    -webkit-animation-name: open-popup-animation; /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 1s; /* Safari 4.0 - 8.0 */
    animation-name: open-popup-animation;
    -webkit-animation-duration: 700ms;
            animation-duration: 700ms;
    overflow: hidden;
    border-bottom: 1px solid #C4C5C9;
}

.notifications-list-popup-closed {
    position: absolute;
    right: 31px;
    width: 350px;
    height: 0;

    -webkit-animation-name: close-popup-animation; /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 1s; /* Safari 4.0 - 8.0 */
    animation-name: close-popup-animation;
    -webkit-animation-duration: 700ms;
            animation-duration: 700ms;
    overflow: hidden;
}

@-webkit-keyframes open-popup-animation {
    from { height: 0; }
    to { height: 400px; }
}

@keyframes open-popup-animation {
    from { height: 0; }
    to { height: 400px; }
}

@-webkit-keyframes close-popup-animation {
    from { height: 400px; }
    to { height: 0; }
}

@keyframes close-popup-animation {
    from { height: 400px; }
    to { height: 0; }
}

.notifications-list {
    background-color: #fff;
    color: #000;
    overflow: auto;
    height: calc(100% - 65px);
    border-left: 1px solid #C4C5C9;
    border-right: 1px solid #C4C5C9;
    box-shadow: 0px 4px 10px 2px rgba(213,214,218,1);
}

.notifications-list::-webkit-scrollbar {
    background: #CCF6ED;
    border-radius: 4px;
    height: 8px;
    width: 8px;
}
  
.notifications-list::-webkit-scrollbar-thumb {
    background: #6a98ee;
    border-radius: 4px;
}

.notifications-icon {
    background-color: #fff;
    padding: 10px;
    cursor: pointer;
    width: 28px;
    height: 28px;
    text-align: center;
    border-radius: 5px;
}

.bell-icon {
    color: #999999;
    font-size: 25px;
}

.notifications-count {
    background-color: #E54C2D;
    color: #fff;
    border-radius: 50%;
    width: 28px;
    height: 28px;
    font-size: 15px;
    display: inline-block;
    position: absolute;
    top: 16px;
    line-height: 28px;
}

.font-13 {
    font-size: 13px;
}

.arrow-up {
    width: 0;
    height: 0;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    border-bottom: 15px solid #6a98ee;
    position: relative;
    left: 310px;
}

.notifications-list-title {
    padding: 15px 20px;
    color: #fff;
    height: 20px;
    text-transform: uppercase;
    background-color: #6a98ee;
}

.animated-notification-enter {
    opacity: 0.01;
}
  
.animated-notification-enter.animated-notification-enter-active {
    opacity: 1;
    background-color: lightgreen;
    transition: opacity 1s ease-in;
}
  
.animated-notification-exit {
    opacity: 1;
    background-color: #E54C2D;
}
  
.animated-notification-exit.animated-notification-exit-active {
    opacity: 0.01;
    transition: opacity 1s ease-in;
}
  
.animated-no-notifications-exit {
    opacity: 1;
}
  
.animated-no-notifications-exit.animated-no-notifications-exit-active {
    opacity: 0;
}

.no-notifications {
    text-align: center;
    margin-top: 40px;
}

@media only screen and (max-width: 460px) {
    .notifications-icon {
        width: 20px;
        height: 20px;
    }

    .arrow-up {
        border-left: 12px solid transparent;
        border-right: 12px solid transparent;
        border-bottom: 12px solid #6a98ee;
    }

    .notifications-list .notification-wrapper {
        font-size: 12px;
    }

    .notifications-list-title {
        height: 15px;
        font-size: 13px;
    }

    .notifications-count {
        font-size: 12px;
    }

    .no-notifications {
        font-size: 14px;
    }
}

@media only screen and (min-width: 360px) and (max-width: 460px) {
    .notifications {
        width: 60px;
    }

    .bell-icon {
        font-size: 22px;
    }

    .notifications-count {
        width: 23px;
        height: 23px;
        line-height: 23px;
        position: absolute;
        top: 20px;
        right: 10px;
    }

    .arrow-up {
        left: 267px;
    }

    .notifications-list-popup,
    .notifications-list-popup-closed {
        width: 300px;
        right: 20px;
    }
}

@media only screen and (max-width: 359px) {
    .notifications {
        width: 56px;
    }

    .bell-icon {
        font-size: 20px;
    }

    .notifications-icon {
        width: 18px;
        height: 18px;
    }

    .notifications-count {
        width: 22px;
        height: 22px;
        line-height: 22px;
        top: 22px;
        right: 8px;
    }

    .arrow-up {
        left: 233px;
    }

    .notifications-list-popup,
    .notifications-list-popup-closed {
        width: 265px;
        right: 17px;
    }
}
.notification-wrapper {
    font-size: 14px;
    padding: 15px;
    padding-bottom: 0;
    border-bottom: 1px solid #dfe3e6;
}

.notification-wrapper > h4 {
    margin: 0;
}

.notification-wrapper > p {
    margin: 15px 0;
}

.notification-wrapper > hr {
    margin: 0;
}

.promo-img {
    width: 150px;
    height: 150px;
    margin: 15px 0;
}

.loading {
    background: transparent url(/notifications-component/static/media/spinner.c539cb21.svg) center no-repeat;
}

.cursor-pointer {
    cursor: pointer;
}

.notification-wrapper:hover {
    background-color: #dfe3e6;
}

@media only screen and (max-width: 460px) {
    .promo-img {
        width: 120px;
        height: 120px;
    }
}
